.card {
  width: fit-content;
  height: auto;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 2px 4px #0000001a;
  box-sizing: border-box;
  overflow: hidden;
}

.card-img {
  width: fit-content;
  height: 26vh;
}
.card-img img {
  width: fit-content;
  height: 50%;
}

.card-body {
  width: fit-content;
  height: 50%;
}

.card-rate {
  width: fit-content;
  margin: 0 0 10px;
  font-size: 1.5rem;
}

.card-title {
  width: fit-content;
  margin: 0 0 10px;
  font-size: 0.9rem;
}

.card-text {
  margin: 0 0 15px;
}

.card-para {
  font-size: 12px;
}
